@import "../node_modules/bootstrap/scss/bootstrap";
@import "../node_modules/bootstrap-icons/font/bootstrap-icons.css";

body {
  margin: 0;
	padding: 0;
  font-family: Ubuntu,Open Sans,sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

label {
  display: block;
}

.form-select {
  width: 100%;
}

.app {
  text-align: center;
}

.map-wrap {
  position: relative;
  width: 100%;
  height: 100vh;
}

.map {
  position: absolute !important;
  width: 100%;
  height: 100%;
}

.watermark {
  position: absolute;
  left: 10px;
  bottom: 10px;
  z-index: 999;
}

.watermark img {
  height: 25px;
}

.controls {
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: 999;
  width: 300px;
  height: auto;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 4px 20px 0 rgba(51,51,89,.2);
  text-align: left;
  padding: 10px 20px 0px 20px;
}

.toast {
  z-index: 1000;
  margin-top: 10px;
  /*left: 65% !important;*/
}

.toast-header {
  justify-content: space-between;
  background-color: transparent;
}

.btn-zoom-toast {
  font-size: .875rem;
  padding: 0;
}

.btn-zoom-country {
  font-size: .875rem;
  padding: 0;
}

.zoom-btn-list {
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  list-style-type: none;
  margin: 0;
}

.txt-name {
  font-size: 20px;
  line-height: 25px;
  font-weight: 700;
}

.txt-label {
  font-size: 16px;
  line-height: 20px;
  padding: 6px;
  margin-top: 3px;
}

.txt-code {
  font-weight: 700!important;
  border: 1px solid rgba(26,34,36,.25);
  line-height: 18px;
  border-radius: 3px;
  box-shadow: 0 1px 0 0 rgb(26 34 36 / 10%);
  font-size: 90%;
  padding: 6px;
}

.wikidata {
  padding: 0 3px;
  vertical-align: middle;
  text-align: left;
}

.wikidata > div.label > span {
  text-transform: capitalize;
  font-weight: bold;
  margin-right: 3px;
}

.wikipedia {
  font-size: 12px;
  line-height: 18px;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /*text-indent: 35px;*/
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
  clear: both;
}

.wikipedia-link {
  text-align: left;
}

.wikipedia-img {
  float: left;
  position: absolute;
  margin-top: 10px;
  vertical-align: middle;
}

