body {
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #6b7c92;
  background-color: #fff;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

h5 {
  margin-top: 20px;
  margin-bottom: 30px;
  font-size: 1.25rem;
  font-family: ubuntu,open sans,sans-serif;
  font-weight: 400;
  line-height: 1.2;
  color: #333359;
}

h6 {
  color: #333359;
  font-weight: 400;
}

h6 > small {
  color: #6B7C93;
  font-weight: 400;
}

hr.border-top {
  border-color: #C1CFE4;
}

small {
  font-size: 0.775em;
}

.form-label {
  margin-bottom: 0.5rem;
}

.form-control {
  border-color: #C1CFE4;
}

.input-group > input.form-control, .form-check-label, .prelabel {
  font-family: open sans,ubuntu,sans-serif;
  font-size: 0.9rem;
  color: #6B7C93;
}

.form-select {
  height: auto;
  width: 100%;
  display: inline-block;
  padding: 0.75rem 2.25rem 0.75rem 0.75rem;
  font-family: var(--bs-font-sans-serif);
  font-size: .875rem;
  font-weight: 400;
  line-height: 1.2;
  color: #333359;
  background-color: #fff;
  background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnIHZpZXdCb3g9JzAgMCAxNiAxNic+PHBhdGggZmlsbD0nbm9uZScgc3Ryb2tlPScjMzMzMzU5JyBzdHJva2UtbGluZWNhcD0ncm91bmQnIHN0cm9rZS1saW5lam9pbj0ncm91bmQnIHN0cm9rZS13aWR0aD0nMicgZD0nTTIgNWw2IDYgNi02Jy8+PC9zdmc+);
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 16px 12px;
  border: 1px solid #c1cfe4;
  border-radius: 4px;
  appearance: none;
  word-wrap: normal;
  text-transform: none;
  margin: 0;
}

.form-check-input {
  appearance: none;
    background-color: #fff;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: contain;
    border: 2px solid #c1cfe4;
    color-adjust: exact;
    height: 1.5em;
    margin-top: 0;
    vertical-align: top;
    width: 1.5em;
}

.form-switch .form-check-input {
  height: 21px;
  width: 2.375em;
  background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='3' fill='rgba%2849,112,254, 1%29'/></svg>");
}

.form-check-inline > .form-check-input {
  width: 1.2em;
  height: 1.2em;
}

.form-check-inline > .form-check-input:checked {
  border-width: 4px;
  border-color: #3170FE;
  background-color: #fff;
}

.form-switch > label {
  padding-left: .5em;
}

.form-check-inline {
  margin-right: 0.5rem;
}

.form-check-inline > label {
  padding-left: -0.5em;
}

#adm, #zip {
  margin-top: 1em;
}

.mb-3 {
  margin-bottom: 1.875rem!important;
}

.input-group > input {
  border-right: 0px;
}

.input-group-text {
  background-color: #FFFFFF;
  border-left: 0px;
  color: #6B7C93;
}

.prelabel {
  padding-right: .5em;
}

.list-group-item-action {
  // Hover state
  &:hover,
  &:focus {
    background-color: #EFF3FB;
    cursor: pointer;
  }

  &:active {
    background-color: #D3DBEC;
  }
}

.countriesList {
  margin-top: -30px;
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  max-height: 300px;
  overflow-y: auto;
  z-index: 1;
}

.countriesList > li{
  font-weight: normal;
  font-size: 0.8em;
  border-color: #fff;
}

#searchContainer {
  position: relative;
}