body {
  margin: 0;
  padding: 0;
  font-family: Ubuntu, Open Sans, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

label {
  display: block;
}

.form-select {
  width: 100%;
}

.app {
  text-align: center;
}

.map-wrap {
  width: 100%;
  height: 100vh;
  position: relative;
}

.map {
  width: 100%;
  height: 100%;
  position: absolute !important;
}

.watermark {
  z-index: 999;
  position: absolute;
  bottom: 10px;
  left: 10px;
}

.watermark img {
  height: 25px;
}

.controls {
  z-index: 999;
  width: 300px;
  height: auto;
  text-align: left;
  background-color: #fff;
  border-radius: 4px;
  padding: 10px 20px 0;
  position: absolute;
  top: 10px;
  left: 10px;
