    position: absolute;
    top: 0;
    right: 0;
  }

  .maplibregl-ctrl-attrib summary.maplibregl-ctrl-attrib-button {
    -webkit-appearance: none;
    appearance: none;
    list-style: none;
  }

  .maplibregl-ctrl-attrib summary.maplibregl-ctrl-attrib-button::-webkit-details-marker {
    display: none;
  }

